@import url("https://fonts.googleapis.com/css?family=Allerta+Stencil");
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

html {
  font-size: 62.5%; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* All focus */
a:focus,
button:focus,
input:focus,
textarea:focus,
select:focus {
  outline: none; }

html {
  font-size: 62.5%; }

button,
input,
textarea {
  color: #000;
  background: transparent;
  border: 0; }

* {
  box-sizing: border-box; }

#Timeline .year line, #Timeline .dot {
  transition: all 0.2s ease; }

body {
  font-family: "Avenir", sans-serif;
  scroll-behavior: smooth;
  background: #1b232d; }

.App {
  display: flex; }

.Filters {
  border: 2px #555 solid;
  width: 200px;
  position: fixed;
  top: 0;
  left: 0; }
  .Filters h2 {
    font-size: 20px;
    font-size: 2rem;
    margin: 10px; }
  .Filters .filterBox {
    display: flex;
    flex-direction: column;
    margin: 25px; }
    .Filters .filterBox .filterType {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center; }
    .Filters .filterBox h3 {
      font-size: 14px;
      font-size: 1.4rem;
      margin: 0 10px 0 0; }
    .Filters .filterBox .filter {
      display: flex;
      flex-direction: column; }
  .Filters input {
    border: 1px red solid; }

.Pop {
  background: #666;
  color: #ccc;
  border-radius: 5px;
  min-width: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 14px;
  font-size: 1.4rem;
  position: fixed;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.2s ease; }
  .Pop p {
    margin: 5px 10px; }
    .Pop p span {
      color: #fff;
      font-weight: bold; }

.Map .infos {
  position: fixed;
  top: 25px;
  left: 25px;
  color: #fff;
  font-size: 20px;
  font-size: 2rem; }
  .Map .infos span {
    color: #cea152;
    text-transform: uppercase; }
  .Map .infos a {
    color: brown; }

.Map svg {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  /*
 * Additional style rules
 *
 * The following are examples of colouring countries.  You can substitute these with your own styles to colour the countries on the map.
 *
 * Color a few countries:
 *
 * .gb, .au, .nc
 * {
 *    fill:       #ff0000;
 * }
 *
 * Color a few small country circles (and the countries):
 *
 * .ms, .ky
 * {
 *    opacity:    1;
 *    fill:       #ff0000;
 * }
 *
 */
  /*
 * Antarctica
 */
  /*
 * Sea
 */ }
  .Map svg .circle {
    opacity: 0;
    fill: #b9b9b9;
    fill-opacity: 1;
    stroke: black;
    stroke-width: 0.99986893;
    stroke-miterlimit: 3.97446823;
    stroke-dasharray: none;
    stroke-opacity: 1; }
  .Map svg .land {
    fill: #b9b9b9;
    fill-opacity: 1;
    stroke: transparent;
    stroke-opacity: 1;
    stroke-width: 5;
    stroke-miterlimit: 3.97446823;
    stroke-dasharray: none; }
    .Map svg .land:hover {
      stroke: #fff; }
  .Map svg .ocean {
    opacity: 1;
    color: #000000;
    fill: #1b232d !important;
    fill-opacity: 1;
    fill-rule: nonzero;
    stroke: #000000;
    stroke-width: 0.5;
    stroke-linecap: butt;
    stroke-linejoin: miter;
    marker: none;
    marker-start: none;
    marker-mid: none;
    marker-end: none;
    stroke-miterlimit: 1;
    stroke-dasharray: none;
    stroke-dashoffset: 0;
    stroke-opacity: 1;
    visibility: visible;
    display: inline;
    overflow: visible; }
  .Map svg .lake {
    fill: #ffffff;
    fill-opacity: 1;
    stroke: #ffffff;
    stroke-opacity: 1;
    stroke-width: 0.99986997;
    stroke-miterlimit: 3.97446823;
    stroke-dasharray: none; }

.kosovo {
  stroke: none; }

.MapFilters {
  color: #fff;
  font-size: 20px;
  font-size: 2rem;
  position: fixed;
  bottom: 25px;
  left: 25px; }
  .MapFilters li {
    text-transform: uppercase;
    padding: 10px 0;
    cursor: pointer; }
    .MapFilters li:active {
      color: #cea152; }

.MapLegend {
  color: #fff;
  position: fixed;
  top: 50px;
  right: 50px; }
  .MapLegend .legend {
    width: 25px;
    height: 150px;
    background: linear-gradient(#8fb7e8, #354456);
    border-radius: 25px; }
  .MapLegend span {
    width: 50px;
    text-transform: uppercase;
    font-size: 9px;
    font-size: 0.9rem;
    position: absolute;
    left: -60px; }
    .MapLegend span:nth-of-type(1) {
      top: 10px; }
    .MapLegend span:nth-of-type(2) {
      bottom: 10px; }

.MapPop {
  background: #666;
  color: #ccc;
  border-radius: 5px;
  min-width: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 14px;
  font-size: 1.4rem;
  position: fixed;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.2s ease;
  z-index: 3; }
  .MapPop p {
    margin: 5px 10px; }
    .MapPop p span {
      color: #fff;
      font-weight: bold; }

#Timeline {
  border: 2px #555 solid;
  position: fixed;
  top: 0;
  left: 200px; }
  #Timeline:hover ~ .Pop {
    opacity: 1; }

#Timeline .year line {
  stroke: #888;
  stroke-width: 4;
  cursor: pointer; }
  #Timeline .year line:hover {
    stroke: #777;
    stroke-width: 12; }

#Timeline .dot {
  fill: #666;
  cursor: pointer; }

#Timeline .dot[data-field="0"] {
  fill: #ff9999; }

#Timeline .dot[data-field="1"] {
  fill: #fff799; }

#Timeline .dot[data-field="2"] {
  fill: #aaff99; }

#Timeline .dot[data-field="3"] {
  fill: #99ffe6; }

#Timeline .dot[data-field="4"] {
  fill: #99bbff; }

#Timeline .dot[data-field="5"] {
  fill: #d599ff; }

#Timeline .dot[data-field="6"] {
  fill: #ff99cc; }
